import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        showTable: true,
        showMenu: true,
        showTimingMenu: true,
        showMsg: true,
        showSetting: false,
        settingTable: true,
        gonghaoUpdate: false,
        menuUpdate: false,
        timingMenuUpdate: false,
        messageUpdate: false,
        settingUpdate: false
    },
    mutations: {
        changeShowTable: (state, data) => {
            state.showTable = data;
        },
        changeMenuTable: (state, data) => {
            state.showMenu = data;
        },
        changeTimingMenuTable: (state, data) => {
            state.showTimingMenu = data;
        },
        changeMsgTable: (state, data) => {
            state.showMsg = data;
        },
        changeShowSetting: (state, data) => {
            state.showSetting = data;
        },
        changeSettingTable: (state, data) => {
            state.settingTable = data;
        },
        changeGonghaoUpdate: (state, data) => {
            state.gonghaoUpdate = data;
        },
        changeMenuUpdate: (state, data) => {
            state.menuUpdate = data;
        },
        changeTimingMenuUpdate: (state, data) => {
            state.timingMenuUpdate = data;
        },
        changeMsgUpdate: (state, data) => {
            state.messageUpdate = data;
        },
        changeSettingUpdate: (state, data) => {
            state.settingUpdate = data;
        }
    },
    actions: {}
});


