<template>
    <div class="menu-setting">
        <div v-if="showTimingMenu">
          <el-button class="create" type="primary" @click="create">配置新定时菜单栏</el-button>
          <el-table :data="menuList" border style="width: 100%">
              <el-table-column header-align="center" align="center" prop="title" label="标题">
              </el-table-column>
              <el-table-column header-align="center" align="center" prop="date" label="定时时间">
              </el-table-column>
              <el-table-column label="操作">
                  <template slot-scope="scope">
                      <el-button @click="updateOne(scope.row)" type="primary" size="small">编辑</el-button>
                      <el-button @click="deleteOne(scope.row, scope.$index)" type="danger" size="small">删除</el-button>
                  </template>
              </el-table-column>
          </el-table>
        </div>
        <edit-info v-else @showAgain="showAgain" :data_info="isCreate ? {} : data_info">{{isCreate ? '配置新定时菜单栏' : '修改定时菜单栏配置'}}</edit-info>
    </div>
</template>

<script>
    import editInfo from "./timingMenu/editInfo.vue";
    import store from "../../store.js";
    import Axios from "axios";
    export default {
      components: {
        editInfo
      },
      computed: {
        showTimingMenu() {
          return store.state.showTimingMenu;
        },
        timingMenuUpdate() {
          return store.state.timingMenuUpdate;
        }
      },
      data() {
        return {
          isCreate: true,
          menuList: [],
          data_info: {},
          menuIndex: ""
        };
      },
      mounted() {
        this.showMenuList();
      },
      methods: {
        create() {
          store.commit("changeTimingMenuTable", false);
        },
        showMenuList() {
          Axios({
            url: "/menuTime/",
            method: "get"
          }).then(res => {
            this.menuList = res.data.data;
          });
        },
        updateOne(row) {
          this.isCreate = false;
          this.data_info = row
          store.commit("changeTimingMenuUpdate", true);
          store.commit("changeTimingMenuTable", false);
        },
        deleteOne(row, index) {
          Axios({
            url: "/menuTime/del",
            method: "get",
            params: {
              id: row._id
            }
          }).then(res => {
            let that = this;
            store.commit("changeTimingMenuUpdate", false);
            this.$confirm("确认删除？")
              .then(_ => {
                that.showMenuList();
                that.$message({
                  type: "success",
                  message: res.data.success
                });
              })
              .catch(_ => {});
          });
        },
        showAgain() {
          this.showMenuList();
        }
      }
    };
</script>

<style scoped>
    .menu-setting {
      padding: 30px;
      margin-left: 200px;
    }
    .create {
      margin-bottom: 30px;
    }
</style>



